@font-face {
  font-family: 'GT Walsheim Pro';
  src: local('GT Walsheim Pro'), url(/order-box/static/media/GTWalsheimPro-Medium.86882f7d.woff) format('woff');
}

* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  font-family: 'GT Walsheim Pro';
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
  font-family: 'GT Walsheim Pro';
}

.App {
  font-family: sans-serif;
  text-align: center;
  display: grid;
  grid-template-columns: 1.3fr 1fr;
  background: #f0e3ff;
  font-family: 'GT Walsheim Pro';
  /* box-shadow: inset 0 50px 70px rgba(216, 147, 18, 0.9); */
}

.product-list {
  padding: 20px;
}

.total {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  color: white;
  padding: 10px;
}
.products_product__1hNBC {
    display: inline-flex;
    justify-content: space-around;
    background: #70559b;
    color: white;
    padding: 12px;
    border-radius: 6px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.8);
    margin: 12px;
    width: 300px;
    align-items: center;
    cursor: pointer;
}

.products_product__1hNBC:hover {
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.9);
    transition: 500ms ease-in-out;
}

.products_icon__1J8oZ {
    width: 100px;
    height: 100px;
}

.products_itemTitle__GhQ5W {
    font-size: 26px;
}
.basket_basket__27Fhu {
    background: #d89cf6;
    min-height: 100vh;
    padding: 20px;
}

.basket_basket__27Fhu .basket_product__2dVx5 {
    background: #916dd5;
}


.basket_meal__1SuPm {
    border: 1px solid;
    border-radius: 6px;
    margin: 0 15px 12px 15px;
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.6);
}

.basket_title__1lcmr {
    margin-top: 0;
}

.basket_total__1rz0j {
    display: flex;
    justify-content: space-around;
    font-size: 26px;
    margin: 0 0 1rem 20rem;
}

.basket_crossBtn__odwNW {
    background: none;
    border: none;
    display: flex;
    align-self: center;
    cursor: pointer;
}

.basket_item__1TDXY {
    width: 50%;
}
.App {
  text-align: center;
}

.icon {
  width: 100px;
  height: 100px;
}

h3 {
  margin-top: 0;
}
